
  .messageBoard{
    height:30rem;
    /*padding-right: 145px;*/
    width:100%;
    position:relative;
}
.messageBoard__headerButton{
  padding-right: 3px;
  max-height: 40px;
  min-height: 40px;
  min-width: 100%;
  width:100%;
  background-color: #1062fe !important;
  color:white !important;
  transition: border 280ms cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 280ms cubic-bezier(0.4, 0, 0.2, 1);
  -webkit-box-shadow: 0px 1px 2px 0px rgb(60 64 67 / 30%),
    0px 1px 3px 1px rgb(60 64 67 / 15%);
  box-shadow: 0px 1px 2px 0px rgb(60 64 67 / 30%),
    0px 1px 3px 1px rgb(60 64 67 / 15%);
}
.messageBoard__headerButton:hover {
    transition: border 280ms cubic-bezier(0.4, 0, 0.2, 1),
      box-shadow 280ms cubic-bezier(0.4, 0, 0.2, 1);
    -webkit-box-shadow: 0px 4px 4px 0px rgb(60 64 67 / 30%),
      0px 8px 12px 6px rgb(60 64 67 / 15%);
    box-shadow: 0px 4px 4px 0px rgb(60 64 67 / 30%),
      0px 8px 12px 6px rgb(60 64 67 / 15%);
    background-color: #1458ce !important;
    color:white !important;
}
.messageBoard__message__lists{
    overflow-y: scroll;
    min-height:80%;
    max-height:80%;

}

.messageBoard__form {
  min-height: 10%;
  bottom:0;
  position:absolute;
  width:100% !important;
  max-width:100%;

  background-color: rgb(24, 23, 23);

  display: flex;
  justify-content: flex-start;
  font-size: 1.5rem;
    border-bottom-left-radius: 20px;
}

.messageBoard__button {

    background-color: rgb(56, 56, 143);
}


.messageBoard__input {
  line-height: 1.5;
  width: 80%;
  font-size: 1rem;
  background: rgb(58, 58, 58);
  color: white;
  outline: none;
  border: none;
  padding: 0 0 0 20px;
    border-bottom-left-radius: 20px;
}

.messageBoard__button{
  background-color: #282c34; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  font-size: 1.25rem;
    min-width: 175px;
}

.messageBoard__button :disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.message__text {
  word-wrap:break-word;
  max-width: 500px;
  margin-bottom: 12px;
    margin-left: 8px;
    margin-right: 8px;
  line-height: 24px;
  padding: 10px 20px;
  border-radius: 25px;
  position: relative;
  color: white;
  text-align: center;
}

.message {
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.message:hover {
    background-color: #ebebeb;
}

.messageImageAndName{
    position:relative;
    width:auto;
}
.messageImageAndName__name__sent{
    position:absolute;
    z-index:10001;
    right:0px;
    width:200px !important;
    border-radius: 999px !important;
    background-color:grey !important;
    color: white !important
}
.messageImageAndName__name__received{
    z-index:10001;
    left:0px;
    width:200px !important;
    border-radius: 999px !important;
    background-color: grey !important;
    color: white !important
}
.sent {
  flex-direction: row-reverse;
}

.sent p {
  color: white;
  background: #0b93f6;
  align-self: flex-end;
}
.received p {
  background: #e5e5ea;
  color: black;
}

.message__image{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: 2px 5px;
}
@media (max-width: 800px) {
    .messageBoard__form {
        border-radius: 0;
    }

    .messageBoard__input {
        border-radius: 0;
    }

}
